import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { API_CONFIG } from '../../../../config/api.config';
import { CreateVeiculoDto, VeiculoDto, SearchVeiculoDto, UpdateVeiculoDto } from '../../interfaces/veiculos/veiculo.dto';
import { PageableDto, PageOptionsDto } from '../../interfaces/others/pageable.dto';
import { PageUtils } from '../../utils/PageUtils';

@Injectable({
  providedIn: 'root'
})
export class VeiculosService {

  constructor(
    private http: HttpClient,
  ) { }

  list(filterDto?: SearchVeiculoDto, pageOptions = new PageOptionsDto()): Observable<PageableDto<VeiculoDto>> {
    let params = PageUtils.getPageOptionsParams(pageOptions);

    if (filterDto) {
      if (filterDto.searched_veiculo) {
        params = params.append('searched_veiculo', filterDto.searched_veiculo);
      }
      if (filterDto.ano) {
        params = params.append('ano', filterDto.ano);
      }
      if (filterDto.cor) {
        params = params.append('cor', filterDto.cor);
      }
      if (filterDto.fabricante) {
        params = params.append('fabricante', filterDto.fabricante);
      }
      if (filterDto.modelo) {
        params = params.append('modelo', filterDto.modelo);
      }
      if (filterDto.placa) {
        params = params.append('placa', filterDto.placa);
      }
      if (filterDto.responsavel_id) {
        params = params.append('responsavel_id', filterDto.responsavel_id);
      }
      if (filterDto.tipo) {
        params = params.append('tipo', filterDto.tipo);
      }
    }

    return this.http.get<PageableDto<VeiculoDto>>(`${API_CONFIG.baseURL}/veiculos`, {
      responseType: 'json',
      params,
    });
  }

  create(newVeiculo: CreateVeiculoDto): Observable<VeiculoDto> {
    return this.http.post<VeiculoDto>(
      `${API_CONFIG.baseURL}/veiculos`,
      newVeiculo,
      {
        responseType: 'json',
      });
  }

  update(veiculo_id: string, dto: UpdateVeiculoDto): Observable<VeiculoDto> {
    return this.http.put<VeiculoDto>(
      `${API_CONFIG.baseURL}/veiculos/${veiculo_id}`,
      dto,
      {
        responseType: 'json',
      });
  }

  delete(veiculo_id: string): Observable<any> {
    return this.http.delete<any>(
      `${API_CONFIG.baseURL}/veiculos/${veiculo_id}`,
      {
        responseType: 'json',
      });
  }
}
